import React, { Component } from 'react';
// import logo from './logo.svg';
// <img src={logo} className="App-logo" alt="logo" />
import Intro from './containers/Intro';
import AboutMe from './containers/AboutMe';
import './styles/App.css';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
    	hover: 'skill',
    }
    this.handleHoverStatus = this.handleHoverStatus.bind(this);
  }

  handleHoverStatus(mouseLoc){
    this.setState({ hover: mouseLoc});
  }

  render() {
  	const { hover } = this.state;
    return (
      <div className="App">
        <Intro/>
        <AboutMe hover={ hover } handleHoverStatus={this.handleHoverStatus}/>
      </div>
    );
  }
}

export default App;



