export const testTextReducer = (state = 'default text', action) => {
	switch(action.type) {
       case 'ADD_TEXT' :
         return action.text;
        case 'REMOVE_TEXT' :
          return action.text;
       default:
         return state;
	}
}

export const testReducer = (state = true, action) => {
  switch(action.type) {
  	case 'TEST' :
  	 return !action.status;
  	default :
  	  return state;
  }
}


