import React from 'react';
import headPhoto from '../images/maguangchen.jpg';

import Skill from '../component/Skill';
import Personality from '../component/Personality';
import Experience from '../component/Experience';

const AboutMe = (props) => {
	const changeToSkill = () => {
     const status = 'skill';
     props.handleHoverStatus(status);
	}
	const changeToPersonality = () => {
     const status = 'personality';
     props.handleHoverStatus(status);
	}
	const changeToExperience = () => {
     const status = 'Experience';
     props.handleHoverStatus(status);
	}
	const status = props.hover;
	const content = status === 'skill' ? <Skill />
	: status === 'personality' ? <Personality /> : <Experience />
	return (
      <div className="about_me">
        <img 
        className="about_me_photo"
        src={headPhoto} alt="Ma Guang Chen"/>
        <h1>About 馬廣辰</h1>
        <div className="about_me_icon-area">
          <a onMouseOver={changeToSkill} href="/"><i className="fa fa-code skill">Skill</i></a>
          <a onMouseOver={changeToPersonality} href="/"><i className="fa fa-user-circle personality">Personality</i></a>
          <a onMouseOver={changeToExperience} href="/"><i className="fa fa-suitcase experience">Experience</i></a>
        </div>
        <div className="about_me_content-area">
          <div className="about_me_content-area_content">{content}</div>
        </div>
      </div>
	)
}

export default AboutMe;


